<template>
  <!--API-->
    <section>
      <ul v-for="SuperMan in SuperMans" class="pp">
<!---->
        <li >: {{SuperMan.city}}</li>
        <!---->
        <li v-text="SuperMan.user_name">:</li>
        <!---->
        <li >: {{SuperMan.avatar}}</li>
        <!--id-->
        <li v-text="SuperMan.id">id:</li>
        <!---->
        <li v-text="SuperMan.create_time">: </li>
        <!---->
        <li v-text="SuperMan.admin">: </li>
        <!---->
        <li v-text="SuperMan.__v"></li>
        <!---->
        <li v-text="SuperMan.status"></li>
      </ul>
    </section>
</template>

<script>
  import  Vue from 'vue'
    export default {
        name: "AskFor",
      data:function () {
          return {
            //
             username:'',
               //
            city:'',
            //
            avatar:'',
            //
             SuperMans:[]

          }
      },
      created(){
          Vue.axios.get("https://elm.cangdu.org/admin/all").then (res =>{
            this.SuperMans = res.data.data;
            //
            console.log(this.SuperMans);
          })
 // console.log(admins.data);
        }
      }

</script>

<style scoped>

  .pp{
    text-align: center;
  }
</style>



