<template>
  <!--li-->
    <section>
      <ul v-for="li in lis">
        <li> {{li.category}}</li>
        <li>{{li.delivery_mode.text}}</li>
        <li>{{li.description}}</li>
        <li>{{li.distance}}</li>
        <li><img src="" alt="">{{li.image_path}}</li>
        <li>{{li.description}}</li>
        <li>{{li.description}}</li>
        <li>{{li.description}}</li>
      </ul>
    </section>
</template>

<script>
  import Vue from 'vue'

    export default {
        name: "Change",
      data : function () {
        return {
          lis:[] //
        }
      },
      created () {
         Vue.axios.get('https://elm.cangdu.org/v4/restaurants?geohash=31.22967,121.4762&keyword=').then (res=>{
           //lis 
           this.lis = res.data;
           //
           console.log(this.lis);
         })
      }
    }
</script>

<style scoped>

</style>



